@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  * {
    @apply antialiased;
  }

  body {
    @apply bg-slate-950 text-slate-50;
    font-feature-settings: 'cv06', 'cv07', 'cv11';
  }

  /* RTL Support */
  [dir="rtl"] {
    @apply text-right;
  }

  [dir="rtl"] .ltr-only {
    @apply hidden;
  }
}

@layer components {
  .card-base {
    @apply bg-slate-900 border border-slate-800 rounded-2xl p-6 transition-all duration-300;
  }

  .card-hover {
    @apply hover:bg-slate-800 hover:border-slate-700 hover:shadow-lg hover:shadow-slate-900/50;
  }

  .text-primary {
    @apply text-slate-50;
  }

  .text-secondary {
    @apply text-slate-300;
  }

  .text-tertiary {
    @apply text-slate-500;
  }

  .badge-success {
    @apply bg-emerald-500/10 text-emerald-400 px-3 py-1 rounded-full text-xs font-semibold border border-emerald-500/20;
  }

  .badge-warning {
    @apply bg-rose-500/10 text-rose-400 px-3 py-1 rounded-full text-xs font-semibold border border-rose-500/20;
  }
}
